import React from 'react';

const getRe = (ingredientsToSearchFor) => (
  new RegExp(
    '('
      + ingredientsToSearchFor.map(entry => entry.synonyms.join('|')).join('|')
      + ')',
    'gi',
  )
);

const getClassName = (index) => (
  index % 2 === 1
    ? 'UserCorrectionMenu__FlaggedIngredient'
    : 'UserCorrectionMenu__UnflaggedIngredient'
);

export default (ingredients, searchableIngredients) => {
  const ingredientsToSearchFor = searchableIngredients
    .filter(entry => entry.shouldSearch);
  const re = getRe(ingredientsToSearchFor);
  const ingredientArr = ingredients.split(re);
  return ingredientArr.map((ingredient, index) => (
    <span className={getClassName(index)}>{ingredient}</span>
  ));
};



